/// solution

function isSorted(array) {
  for (let i = 0; i < array.length; i++) {
    let item = array[i]
    let next = array[i + 1]
    if (i < array.length && item > next) {
      return false
    }
  }
  return true
}

/// tests

import { test } from 'ava'

test(t => t.is(isSorted([]), true))
test(t => t.is(isSorted([-9, -5, 0, 3, 9]), true))
test(t => t.is(isSorted([3, 9, -3, 10]), false))
